import React from "react";
import "../../componentsCSS/services/ServicesStyle2.css";

export const ServicesStyle2 = ({ primaryColor, secondaryColor }) => {
  const services = [
    {
      title: "Dveloppement Web",
      content: "Nous crons des sites web modernes, adaptatifs et optimiss pour le SEO.",
    },
    {
      title: "SEO & Marketing",
      content: "Augmentez votre visibilit avec des stratgies SEO et marketing digital avances.",
    },
    {
      title: "Branding & Design",
      content: "Construisez une marque mmorable avec un design unique et percutant.",
    },
  ];

  return (
    <div
      className="services-style-2"
      style={{
        "--primary-color": primaryColor,
        "--secondary-color": secondaryColor,
      }}
    >
      <h2>Nos Services</h2>
      <div className="cards">
        {services.map((service, index) => (
          <div key={index} className={`card ${index % 2 === 0 ? "left" : "right"}`}>
            <h3>{service.title}</h3>
            <p>{service.content}</p>
          </div>
        ))}
      </div>
    </div>
  );
};
